.customers {
  @extend %section;
  max-width: 480px;
  padding-top: 60px;
  padding-bottom: 60px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
  background-image: url(../images/customers/bcgcustomers.png);
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/customers/bcgcustomers2x.png);
  }
  @include tablet {
    max-width: 768px;
    padding-top: 60px;
    padding-bottom: 120px;
    background-position: bottom center;
    background-repeat: no-repeat;
    background-image: url(../images/customers/bcgcustomerstablet1x.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/customers/bcgcustomerstablet2x.png);
    }
  }
  @include desktop {
    max-width: 1280px;
    padding-top: 100px;
    padding-bottom: 255px;
    height: 652px;
    background-position: top center;
    background-repeat: no-repeat;
    background-image: url(../images/customers/bcgcustomerdesk1x.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/customers/bcgcustomerdesk2x.png);
    }
  }
}
.customers__container {
  @extend %container;
}
.customers__title {
  margin-bottom: 20px;
  @media screen and (max-width: 767px) {
    font-weight: 700;
    font-size: 42px;
    line-height: 49px;
  }
}
.customers__text {
  max-width: 480px;
  margin-bottom: 32px;
  @media screen and (max-width: 767px) {
    font-size: 16px;
    line-height: 19px;
  }
}

.stars__list {
  @extend %list;
  display: flex;
  margin-bottom: 36px;
}

.reviews-list__item {
  padding: 8px;
  background-color: #ffff;
  border-radius: 0px 30px 30px 30px;

  @media screen and (min-width: 768px) {
    padding: 12px;
  }
}

.reviews-list {
  @extend %list;
  display: flex;
  gap: 32px;

  @media screen and (max-width: 767px) {
    flex-direction: column;
    gap: 24px;
  }
  @include desktop {
    max-width: 1141px;
    gap: 139px;
  }
}

.reviews-list__text {
  // width: 264px;
  // height: 95px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.19;
  color: #2c2c2c;
  margin-bottom: 32px;
  @media screen and (max-width: 767px) {
    font-size: 18px;
    line-height: 21px;
  }
}
.personaldate {
  display: block;
  padding-right: 18px;
  margin-left: 16px;
  border-right: 1px solid #7eb101;
}
.infoclient {
  display: flex;
  align-content: center;
}
.personaldate__title {
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  color: #2c2c2c;
}
.personaldate__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: #2c2c2c;
}
